import { useState } from 'react';
import { Link } from 'react-router-dom';
import useWindowSize from '../useWindowSize';
import { motion } from 'framer-motion';
import './Navbar.css';

const Nav = (props) => {

    return (
        <Link to={props.uri} style={{ textDecoration: 'none' }}>
            <div id='nav-button'>
                <h3 style={{fontFamily:'GentiumBI', color: 'black', fontSize:'20px'}}>{props.title}</h3>
            </div>
        </Link>
    );

}

const Navbar = () => {

    const size = useWindowSize();   
    const [collapse, setCollapse] = useState(true);

    const animate = {
        open: {
            opacity: 1,
            rotateX: 0,
            transition: {
                duration: 0.5
            },
            display: 'block'
        },
        close: {
            opacity: 0,
            rotateX: -15,
            transition: {
                duration: 0.3,
                delay: 0.1
            },
            transitionEnd: {
                display: 'none'
            }
        }
    };

    return (
        <>
        { size.width > 767 ? 
            <div id='nbar' >
                <Nav title='Home' uri='/' />
                <Nav title='Magici frutti' uri='/prodotti' />
                <Nav title='Chi siamo' uri='/chi-siamo' />
                <Nav title='Fattoria didattica' uri='/fattoria-didattica'  />
                <Nav title='Impegno sociale' uri='/impegno-sociale' />
                <Nav title='Contatti' uri='/contatti' />
            </div>
            :
            <>
            <div style={{width: '30px', height: '50px', position: 'absolute', top: '20px', left: '20px'}} onClick={() => setCollapse(o => !o)}>
                <div style={{width: '30px', height:'3px', backgroundColor: 'white', boxShadow: '0px 0px 3px rgba(0,0,0,0.3), 0px 0px 3px rgba(0,0,0,0.3)', borderRadius: '20%'}}/>
                <div style={{width: '30px', height:'3px', backgroundColor: 'white', boxShadow: '0px 0px 3px rgba(0,0,0,0.3), 0px 0px 3px rgba(0,0,0,0.3)', marginTop: '3px', borderRadius: '20%'}}/>
                <div style={{width: '30px', height:'3px', backgroundColor: 'white', boxShadow: '0px 0px 3px rgba(0,0,0,0.3), 0px 0px 3px rgba(0,0,0,0.3)', marginTop: '3px', borderRadius: '20%'}}/>
            </div>
            <motion.div initial='close' animate={collapse ? 'close' : 'open'} variants={animate} id='nbar'>
                <Nav title='Home' uri='/' />
                <Nav title='Magici frutti' uri='/prodotti' />
                <Nav title='Chi siamo' uri='/chi-siamo' />
                <Nav title='Fattoria didattica' uri='/fattoria-didattica'  />
                <Nav title='Impegno sociale' uri='/impegno-sociale' />
                <Nav title='Contatti' uri='/contatti' />
            </motion.div>
            
            </>
        }
        </>
    );

}

export default Navbar;