import React from 'react';

import Navbar from './Navbar';

import './Header.css';

//import 'bootstrap/dist/css/bootstrap.min.css';
//import { Container, Row, Col } from 'react-bootstrap';

const Header = () => {

    return(
        <>
        <div id='logotitle'>
                <h1 className='nopm' id='title' style={{fontFamily: 'DSZierschrift'}}>Monte Spazzavento</h1>
                <h3 className='nopm' id='subtitle' style={{fontFamily: 'IM Fell English', fontStyle: 'italic'}}>Azienda agricola biologica</h3>
        </div>

        <div id='bgContainer'>
            <img id='bg'
                src={process.env.PUBLIC_URL + '/res/WhatsApp Image 2021-08-30 at 17.37.49.jpeg'} 
                alt='' 
                style={{position: 'absolute',  zIndex: -1, margin: 'auto', width: '100vw', height: 'auto'}}
            />
        </div>

        <Navbar />
        
        </>
    )

}

export default Header;